//https://fontawesome.ru/examples/
import React from 'react';
import PropTypes from 'prop-types';
//import Communities from "./Communities";
import vpslogo from '../img/logo2.png';

import connect from '@vkontakte/vkui-connect';
import { Link, Counter, List, Cell, View, Avatar, ListItem, Div, FixedLayout, Tabs, HorizontalScroll, TabsItem,PanelHeader,Group,Panel,Button,PopoutWrapper,Alert, FormLayout, FormLayoutGroup, Input, Select } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/my.css';
import '../css/font-awesome.min.css';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Message from '@vkontakte/icons/dist/24/message';
import Icon24Users from '@vkontakte/icons/dist/24/users';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';


//import Chats from "./Chats";
let groupsList;
class Home extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			activeTab: 'main',
			page: 1,
			activeTabView : 'view_cell'

		};
		this.activeSort1Tab = this.activeSort1Tab.bind(this);
		this.activeSort2Tab = this.activeSort2Tab.bind(this);
		this.activeSort3Tab = this.activeSort3Tab.bind(this);

		if (this.props.app.homeTab) {
			this.state.activeTab = this.props.app.homeTab;
		} else {
			this.state.activeTab = 'main';
		}
		if (this.props.app.homePage) {
			this.state.page = this.props.app.homePage;
		}


		if (this.props.app.homeTabView) {
			this.state.homeTabView = this.props.app.homeTabView;
		} else {
			this.state.activeTabView = 'view_cell';
		}



	}


	GoToGr()
	{
		//<a href="https://vk.com/public185027673" target="_blank">
		//window.location.assign('https://vk.com/public185027673');
		window.open('https://vk.com/public185027673','_blank');
	}

	activeSort3Tab()
	{
		this.setState({ activeSort1Tab: '' });
		this.props.app.activeSort1Tab = '';

		this.setState({ activeSort2Tab: '' });
		this.props.app.activeSort2Tab = '';

		if (this.state.activeSort3Tab!=='sort3_ask')
		{
			this.setState({ activeSort3Tab: 'sort3_ask' });
			this.props.app.activeSort3Tab = 'sort3_ask';
			this.props.app.tariffs.sort(( a, b ) => b.ramvalue - a.ramvalue);
		}
		else
		{
			this.setState({ activeSort3Tab: 'sort3_desk' });
			this.props.app.activeSort3Tab = 'sort3_desk';
			this.props.app.tariffs.sort(( a, b ) => a.ramvalue - b.ramvalue);
		}
	}
	activeSort2Tab()
	{
		this.setState({ activeSort1Tab: '' });
		this.props.app.activeSort1Tab = '';
		this.setState({ activeSort3Tab: '' });
		this.props.app.activeSort3Tab = '';

		if (this.state.activeSort2Tab!=='sort2_ask')
		{
			this.setState({ activeSort2Tab: 'sort2_ask' });
			this.props.app.activeSort2Tab = 'sort2_ask';
			this.props.app.tariffs.sort(( a, b ) => b.diskvalue - a.diskvalue);
		}
		else
		{
			this.setState({ activeSort2Tab: 'sort2_desk' });
			this.props.app.activeSort2Tab = 'sort2_desk';
			this.props.app.tariffs.sort(( a, b ) => a.diskvalue - b.diskvalue);
		}
	}

	activeSort1Tab()
	{
		//this.setState({ activePanel: 'lk', activeStory: 'lk' })
		this.setState({ activeSort3Tab: '' });
		this.props.app.activeSort3Tab = '';

		this.setState({ activeSort2Tab: '' });
		this.props.app.activeSort2Tab = '';

		if (this.state.activeSort1Tab!=='sort1_ask')
		{
			this.setState({ activeSort1Tab: 'sort1_ask' });
			this.props.app.activeSort1Tab = 'sort1_ask';
			this.props.app.tariffs.sort(( a, b ) => b.price - a.price);
		}
		else
		{
			this.setState({ activeSort1Tab: 'sort1_desk' });
			this.props.app.activeSort1Tab = 'sort1_desk';
			this.props.app.tariffs.sort(( a, b ) => a.price - b.price);
		}
	}

	onChangeGroup(event) {
		this.setState({group_id: event.target.value});
	}

	onChangePrice(event) {
		this.setState({price: event.target.value});
	}

	onChangeReach(event) {
		this.setState({stats_reach: event.target.value});
	}

	onChangeViews(event) {
		this.setState({stats_views: event.target.value});
	}
	onChangeRevenue(event) {
		this.setState({revenue: event.target.value});
	}
	onChangeCategory(event) {
		this.setState({category: event.target.value});
	}


	render() {
		if (this.props.activeTab) {
			this.setState({activeTab: this.props.activeTab});
		}
		return (

			<Panel id={this.props.id}>
				<PanelHeader noShadow={true}>Поиск IT Services</PanelHeader>
				<Group>
				<FixedLayout noShadow={true} vertical="top">
					<Tabs theme="header" type="buttons">

						<HorizontalScroll>

							<TabsItem
								onClick={() => {this.setState({ activeTab: 'main' }); this.props.app.homeTab = 'main';}}
								selected={this.state.activeTab === 'main'}
							>
								<i className="fa fa-home"></i>
							</TabsItem>
							<TabsItem
								onClick={() => {this.setState({ activeTab: 'vps' }); this.props.app.homeTab = 'vps';}}
								selected={this.state.activeTab === 'vps'}
							>
								VPS
							</TabsItem>
							<TabsItem
								onClick={() => {this.setState({ activeTab: 'dedic' }); this.props.app.homeTab = 'dedic';}}
								selected={this.state.activeTab === 'dedic'}
							>
								Dedicated
							</TabsItem>
							<TabsItem
								onClick={() => {this.setState({ activeTab: 'vpn' }); this.props.app.homeTab = 'vpn';}}
								selected={this.state.activeTab === 'vpn'}
							>
								VPN
							</TabsItem>
						</HorizontalScroll>

					</Tabs>
				</FixedLayout>
				{0 && this.state.activeTab !=='' &&
	            <Group style={{ marginTop: 60 }}>
	              <Tabs theme="light">
	                <TabsItem
						onClick={() => {this.setState({ activeTabView: 'view_cell' }); this.props.app.homeTabView = 'view_cell';}}
						selected={this.state.activeTabView === 'view_cell'}
						>
	                  Список
	                </TabsItem>
	                <TabsItem
						onClick={() => {this.setState({ activeTabView: 'view_table' }); this.props.app.homeTabView = 'view_table';}}
						selected={this.state.activeTabView === 'view_table'}
	                >
	                  Таблица
	                </TabsItem>
	              </Tabs>
	            </Group>
                }
                {this.state.activeTab === 'main' &&
					<Div style={{ marginTop: 15 }}>

				            <Group title="Профиль">
						        <Cell
						          photo={this.props.app.state.fetchedUserPhoto}
						          description="Написать в группу:"
						          target="_blank"
						          bottomContent={<a style={{ color: '#42648b' }} href="https://vk.com/public185027673" target="_blank"><Button>Написать сообщение</Button></a>}

						          before={<Avatar src={this.props.app.state.fetchedUserPhoto} size={80}/>}
						          size="l"
						        >
						          {this.props.app.state.fetchedUserName}
						        </Cell>
		                    	<Group title="Сервис">
								     <List>


								         <Cell before={<Icon24Users />} description="IT Services"
								         	onClick={(e) => this.GoToGr(e)}
								         >
								         	Наша группа ВКонтакте
								         </Cell>

								     </List>
								</Group>
				      		</Group>



                    </Div>


                }


				{this.state.activeTab === 'vps' &&



				  <Group style={{ marginTop: 30 }}>
				    <Tabs type="buttons">
				      <HorizontalScroll>
				        <TabsItem style={{ padding: 3 }}>
					        <Button style={{ padding: '0px 9px' }}
					        	level={ this.state.activeSort1Tab === 'sort1_ask' || this.state.activeSort1Tab === 'sort1_desk' ? "primary" : "outline"}
					        	onClick={this.activeSort1Tab}
					        >
					          <i className={ this.state.activeSort1Tab === 'sort1_ask' ? 'fa fa-sort-numeric-desc' : 'fa fa-sort-numeric-asc' }></i> Цена
					        </Button>
					     </TabsItem>

				        <TabsItem style={{ padding: 3 }}>
					        <Button style={{ padding: '0px 9px' }}
					        	level={ this.state.activeSort2Tab === 'sort2_ask' || this.state.activeSort2Tab === 'sort2_desk' ? "primary" : "outline"}
					        	onClick={this.activeSort2Tab}
					        >
					          <i className={ this.state.activeSort2Tab === 'sort2_ask' ? 'fa fa-sort-numeric-desc' : 'fa fa-sort-numeric-asc' }></i> Диск
					        </Button>
					     </TabsItem>

				        <TabsItem style={{ padding: 3 }}>
					        <Button style={{ padding: '0px 9px' }}
					        	level={ this.state.activeSort3Tab === 'sort3_ask' || this.state.activeSort3Tab === 'sort3_desk' ? "primary" : "outline"}
					        	onClick={this.activeSort3Tab}
					        >
					          <i className={ this.state.activeSort3Tab === 'sort3_ask' ? 'fa fa-sort-numeric-desc' : 'fa fa-sort-numeric-asc' }></i> ОЗУ
					        </Button>
					     </TabsItem>


					      <TabsItem style={{ padding: 3 }}>
					        <Button style={{ padding: '0px 9px' }}
					        	level={ this.props.app.state.filtersselected===true ? "primary" : "outline"}
					        	onClick={this.props.go} data-to="filtervps" style={{cursor: 'pointer'}}
					        	>
					          <i className="fa fa-filter"></i>  Фильтры
					        </Button>
						 </TabsItem>
				      </HorizontalScroll>
				   </Tabs>
				</Group>




				}
				{this.state.activeTab === 'vps' && this.props.app.tariffs.length > 0 && this.state.activeTabView !== 'view_table' && this.state.activeTab !=='' &&
						<Group title={this.props.app.tariffs.length ? "Список тарифов" : "Список тарифов пуст"}>
							<Group>
							{ //DevicesList.map(value1 => { if ( value1.name==value.device ) return ( {value1.img} ) } )
							}

							{this.props.app.tariffs.map((value, i) => {return ( <Div>Компания: {value.companyname}<Cell photo={value.url_logo} before={<Avatar src={value.url_logo} size={80}/>} description={value.companyname}  size="l">Диск: {value.diskvalue} Гб<br/>Тип диска: {value.disktype}<br/>ОЗУ: {value.ramvalue} Мб<br/>Частота CPU: {value.cpuhz}<br/>Тип вирт-ии: {value.virtualize}<br/>Частота CPU: {value.cpuhz}<br/>Ядер: {value.cpucount} шт.<br/>Страны: {value.country}<br/>Цена: {value.price} руб<br/><Div style={{ height: 7, padding:0 }}></Div><a href={value.affurl_vps} target="_blank"><Button   size="m"   >Заказать</Button></a><Div style={{ height: 7, padding:0 }}></Div></Cell></Div> ) } )
							}

							</Group>
						</Group>
				}

		        {this.state.activeTab === 'vps' && this.props.app.tariffs.length > 0 && this.state.activeTabView === 'view_table' && this.state.activeTab !=='' &&
		                <Group title={this.props.app.tariffs.length ? "Список тарифов" : "Список тарифов пуст"}>
		                <Div disabled={this.props.app.tariffs ? false : true} style={{ marginTop: 25 }}>
							  <table>
							    <thead>
							      <tr>
							        <th>Компания</th>
							        <th>Диск Гб</th>
							        <th>ОЗУ Мб</th>
							        <th>Кол-во ядер CPU</th>
							        <th>Трафик</th>
							        <th>Виртуа-лизация</th>
							        <th>Страна</th>
							        <th>Цена руб</th>
							        <th>Ссылка</th>


							      </tr>
							    </thead>

									{ this.props.app.tariffs &&

										this.props.app.tariffs.map(value => {
											return (  <tr>
													<td>{value.companyname}</td>
													<td>{value.diskvalue}</td>
													<td> {value.ramvalue}</td>
													<td> {value.cpucount}</td>
													<td>{value.trafficlimit}</td>

													<td>{value.virtualize}</td>
													<td> {value.country}</td>

													<td> {value.price}</td>
													<td> <a href={value.affurl_vps} target="_blank">заказать</a></td>
													</tr> )
										} )
									}

		                  		</table>
						</Div>
						</Group>
				}
				{this.state.activeTab === 'dedic' &&

					<Div style={{ marginTop: 15 }}>
						Страница в разработке
					</Div>
				}

				{this.state.activeTab === 'vpn' &&

					<Div style={{ marginTop: 15 }}>
						Страница в разработке
					</Div>
				}




	            </Group>
			</Panel>
		);
	}
}

export default Home;


